.news {
    margin-top: 40px;

    &__container {
        
        line-height: 1.5;
    }
    &__block {
        display: flex;
        gap: 25px;
        justify-content: center;
        // align-items: center;
        @media(max-width: 760px) {
            flex-direction: column;
        }
    }
    &__image {
        height: 600px;
        width: 40%;
        z-index: 9;
        @media(max-width: 760px) {
            order: 2;
            width: 100%;
            flex: auto;
            margin-top: 10px;
            height: 300px;
        }
    }
    &__image-wrapper{
        position: relative;
        margin-bottom: 20px;
        width: 100%;
        height: 100%;
        position: relative;
        z-index: -1;
        border-radius: 25px;
        background: conic-gradient(from -24.88deg at 54.78% 116.81%, #fcf17735 -39.37deg, #f6ce7036 56.23deg, #cfe5e03e 81.27deg, #fcf17734 320.63deg, #f6ce7037 416.23deg);
    }
    &__image img {
        position: absolute;
        left: 14%;
        top: -30px;
        object-fit: contain;
        width: 80%;
        height: 80%;
        transform: scale(1.3);
    }
    &__info {
        // width: 40%;
        flex: 1 0 40%;
    }
    &__title {
        color: var(--color-active);
        font-weight: 500;
        text-transform: uppercase;
        font-size: 13px;
    }
    &__subtitle {
        font-weight: 500;
        font-size: 42px;
        line-height: 1.2;
    }
    &__list {
        padding-left: 15px;
        list-style-type: disc;
        color: var(--color-text2);
        margin-top: 10px;
        font-size: 18px;
        text-align: start;
    }
    &__list-link {
        color: var(--color-active)
    }
    // &__text-title {
    //     margin-top: 23px;
    //     font-size: 22.5px;
    //     font-weight: 700;
    //     margin-bottom: 15px;
    // }
    &__img {
        margin-top: 20px;
        width: 100%!important;
    }
    &__list-number {
        line-height: 1.5;
        text-align: start;
        padding-left: 20px;
        list-style-type: auto;
        color: var(--color-text2);
        font-size: 18px;
        margin-top: 20px;
    }

}

.section__wrapper {
    display: flex;
}

.block1 {
    flex: 0 0 20%;
}
